:root {
  --primary: #fff;
}

.btn {
  /* margin: 18px; */
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
}

.btn--primary {
  background-color: var(--primary);
  color: #242424;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 7px 10px;
  font-size: 16px;
  
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
  font-family: 'Metropolis', sans-serif;
}

.btn--mobile {
  text-align: center;
  border-radius: 4px;
  width: 80%;
  text-decoration: none;
  font-size: 1.5rem;
  background-color: transparent;
  color: #fff;
  padding: 14px 20px;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}

.btn--wide {
  padding: 12px 64px;
  font-size: 20px;
  font-family: 'Metropolis', sans-serif;
}

.btn--large:hover,
.btn--medium:hover,
.btn--mobile:hover {
  transition: all 0.3s ease-out;
  background: #fff;
  color: #e3b386;
}

.btn--wide:hover {
  color: #e3b386;
  background-color: #f00946;
  transition: all 0.2s ease-out;
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 8px 16px;
  height: 100%;
  width: 100%;
}

.blue {
  background-color: #354a62;
  color: #fff;
  border: none;
}

.red {
  background-color: #b5594b;
  color: #fff;
  border: none;
}

.primary {
  background-color: #242424;
  color: #fff;
  border: none;
}

.primary:hover {
  background-color: #fff;
  color: #242424;
  border: none;
}

.green {
  background: #91aeb0;
  color: #fff;
  border: none;
}

.green:hover {
  background-color: #354a62;
}

.red:hover {
  background-color: #e3b386;
  color: white;
}
@media screen and (max-width: 920px) {
  .btn{
    margin: 10px;
  }
}


@media screen and (max-width: 768px) {


  .btn--medium{
    padding: 7px 10px;
    font-size: 14px;
    margin: 20px;
    font-family: 'Metropolis', sans-serif;
    font-weight: bold;

  }
}